.navbar {
    list-style-type: none;
    padding: 0;
    margin: 0;
    background-color: dimgray;
}
.nav-container {
    position: sticky;
    top: 0;
}
.navbar > li{
    display: inline-block;
}
.navlink {
    color: white;
    display: block;
    text-align: center;
    padding: 10px;
    font-size: medium;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    text-decoration: none;

}